.nav {
    grid-area:n;
    background-color: tan;
    padding: 20px;
    font-size: 20px;
}

.item a{
    color: white;
    text-decoration: none;
}

.item a.activeLink{color: gold;}

.myFriendsTitle{
    margin-top: 100px;
    margin-left: 13px;
    color: firebrick;
}

.myFriendsWrapper{
    display: flex;
    justify-content: space-around;
}

.friendWrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.friendImg img{
    width: 28px;
    height: 30px;
    border-radius: 50%;
}

.friendTitle span{
    font-size: 10px;
    color: white;
}